.home-body {
  padding-top: 40vh;
  padding-bottom: 40vh;
  color: white;
  -webkit-transition: all 1s;
  transition: all 1s
}

.home-texthead {
  font-size: 5vw;
}

.home-text {
  font-size: 3.5vw;

  display: inline-block;
  padding: 15px;
}

.bg {
  background: url(/static/media/homess.20a2aadd.jpg);
  height: 100vh;
  opacity: 0.9;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  -webkit-transition: all 1s;
  transition: all 1s;
  background-blend-mode: overlay;


}

.bg-scroll {
  -webkit-transform: translateY(-150px);
          transform: translateY(-150px);

}

@media only screen and (max-width: 980px) {
  .home-texthead {
    font-size: 8vw;
  }

  .home-text {
    font-size: 5.5vw;
  }
}
.aboutbody {
  padding-top: 100px;
  padding-bottom: 100px;
}

.bgabout {
  background-color: rgb(255, 255, 230);
}

.fontstyle {
  font-family: "Poppins", sans-serif;
  font-size: 10px;
}

.profileImg {
  height: 100%;
  width: 100%;
}

.progressheight {
  height: 3%;
}

.aboutme {
  text-align: right;
}

@media only screen and (max-width: 968px) {
  .aboutme {
    text-align: center;
  }
}
.button-portfolio {
  border-radius: 25px;
  font-size: 1.4vw;
  color: rgb(255, 72, 0);
  border: 2px solid rgb(255, 72, 0);
  padding: 6px 25px;
  background-color: azure;
  margin: 5px;
}

.box-height {
  height: 40vh;
  width: 100%;
}

.project-font {
  padding: 100px;
  font-size: 1.5vw;
  border: 1px solid;

}

.project-font p {
  padding-top: 20px
}

.button-end {
  position: relative;
  top: 80px;
  padding: 10px;
  border-radius: 20px;
  font-size: 1.2vw;
}

.border-end {
  border-bottom: 2px solid gray;
}

.row {
  padding: 8px;
}

@media only screen and (max-width: 720px) {
  .button-portfolio {
    width: 100%;
    font-size: 3vw;
  }

  .button-end {
    font-size: 4vw;
  }

  .project-font {
    font-size: 4vw;
    padding: 20px;
  }

  .button-end {
    top: 2px;
  }
}
.form-styles {
  margin-left: 10%;
  margin-right: 10%;
  border-radius: 5px;
  box-shadow: 0 7px 11px 0 rgba(0, 0, 0, 0.2), 0 9px 23px 0 rgba(0, 0, 0, 0.19);
  padding: 20px;
}
textarea {
  height: 240px;
}
.button {
  width: 150px;
}

@media only screen and (max-width: 980px) {
  .form-styles {
    margin: auto;
    width: 100%;
  }
}
.background {
  background-color: azure;
  background-attachment: fixed;
}

.fa {
  font-size: 35px;
  color: gray;
}

.largetext {
  font-size: 35px;
  color: rgb(255, 71, 26);
}

.largetext:hover {
  color: red;
  text-decoration: none;
}

.navtext {
  font-size: 17px;
}

.navbar {
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}

.navbar-scroll {
  background-color: white;
  box-shadow: 1px 4px 7px -3px rgba(0, 0, 0, 0.72);
  -webkit-box-shadow: 1px 4px 7px -3px rgba(0, 0, 0, 0.72);
  -moz-box-shadow: 1px 4px 7px -3px rgba(0, 0, 0, 0.72);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

@media only screen and (max-width: 980px) {
  .navtext {
    background: white;
    height: 200px;
  }
}
html {
  scroll-behavior: smooth;
}

* {
  font-family: "Montserrat", sans-serif;
}
